import React from 'react';
import { useLocalStorage } from './useLocalStorage';

const TodoContext = React.createContext();

function TodoProvider(props){
  const {
      item: todos, 
      saveItem: saveTodos,
      loading,
      error,
  } = useLocalStorage('TODOS_V1',[]);

  const [searchValue, setSearchValue] = React.useState('');
  const [openModal, setOpenModal] = React.useState(false);  // Declaración de que el modal inicia en false

  const completedTodos = todos.filter(todo => !!todo.completed).length;  
  const totalTodos = todos.length;

  //Buscar ToDo´s
  let searchedTodos = [];  //array que guarda los resultados

  if (!searchValue.length >= 1){
    searchedTodos = todos;
  } else {
    searchedTodos = todos.filter(todo => {
      const todoText = todo.text.toLowerCase();
      const searchText = searchValue.toLowerCase();
      return todoText.includes(searchText);
    }); 
  }

  const addTodo = (text) => {
    const newTodos = [...todos];
    newTodos.push({
      completed: false,
      text,
    });
    saveTodos(newTodos);
  };

  const completeTodo = (text) => {
    const todoIndex = todos.findIndex(todo => todo.text === text);
    //La lista de tareas se pasa a una nueva variable
    const newTodos = [...todos];
    //Validación para cambiar el estastus de un ToDo
    if(newTodos[todoIndex].completed === true){
      newTodos[todoIndex].completed = false;
    } else {
      newTodos[todoIndex].completed = true;
    }
    saveTodos(newTodos);
  };

  const deleteTodo = (text) => {
    const todoIndex = todos.findIndex(todo => todo.text === text);
    //La lista de tareas se pasa a una nueva variable
    const newTodos = [...todos]; //inyección del array previo
    newTodos.splice(todoIndex, 1);
    saveTodos(newTodos);
  };


    return (
        <TodoContext.Provider value={{
            loading,
            error,
            totalTodos,
            completedTodos,
            searchValue,
            setSearchValue,
            searchedTodos,
            addTodo,
            completeTodo,
            deleteTodo,
            openModal,
            setOpenModal,
        }}>
          {props.children}
        </TodoContext.Provider>
    );
}

export { TodoContext, TodoProvider};